@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
.alerta {
  padding: 1rem;
  text-transform: uppercase;
  margin: 1rem 0;
  color: #FFFFFF;
  text-align: center;
  font-weight: 700;
}
.alerta.error {
  background-color: rgb(182, 26, 26);
}
.alerta.exito {
  background-color: #6a9919;
}
.alerta.actualizado {
  background-color: #E08709;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
  text-align: center;
}
table.propiedades thead {
  background-color: #6a9919;
}
table.propiedades tbody tr:nth-child(odd) {
  background-color: #d9d9d9;
}
table.propiedades img {
  width: 15rem;
}
table.propiedades td {
  color: #000000;
}
table.propiedades a {
  padding: 0;
  margin: 0;
  padding: 0.9rem 0rem;
  margin: 1rem;
}
table.propiedades form {
  display: flex;
  justify-content: center;
}
table.propiedades form input {
  padding: 0;
  margin: 0;
  padding: 1.5rem 0rem;
  margin: 1rem;
}

.formulario img {
  width: 20rem;
}